import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ProductModel, ProductService } from '../products/product.service'
import { ProductsListComponent } from '../products/products-list.component'
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    moduleId: module.id,
    selector: 'adminBtns',
    inputs: ['product'],
    template: `<button class="btn btn-danger"
                  (click) = "removeProduct(product.id)">
                  <i class="glyphicon glyphicon-trash"></i>
                </button>
                <a routerLink="/product/edit/{{product.id}}" class="btn btn-info">
                  <i class="glyphicon glyphicon-edit"></i>
                </a>
                <a routerLink="/product/{{product.id}}/{{product.name}}" class="btn btn-success">Details</a>`
})
export class AdminBtnsComponent implements OnInit {
    @Output() productId = new EventEmitter();

    constructor(
      private route: ActivatedRoute,
      private router: Router
    ) { }
    
    ngOnInit() { }
    
    removeProduct(id) {
      this.productId.emit(id)
    }
}